.gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin: 20px auto;
  width: 80%;
}

.gallery-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gallery-item img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}

.gallery-item h3 {
  margin: 10px 0;
  text-align: center;
  font-size: 1.2rem;
}

.gallery-item p {
  text-align: center;
  font-size: 0.9rem;
  color: gray;
}

.popup {
  position: fixed;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 3px solid #2fa3b5;
  padding: 40px;
}

.close-btn-container {
  display: block;
  width: 100%;
}

@media (max-width: 800px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .popup {
    /* Add styles for mobile devices */
    width: 80%; /* reduce the width */
    height: auto; /* allow the height to adjust automatically */
  }
  .gallery {
    grid-template-columns: 1fr;
  }
  .popup-image > img {
    width: 100%;
    height: auto;
  }

  .close-btn-container {
    position: relative;
    text-align: right;
    padding-bottom: 10px;
    width: 20px;
    color: #41adbe;
    font-weight: bold;
    font-size: 1.5rem;
    cursor: pointer;
  }
}
