nav {
  width: 100%;
  background-color: #fff;
  color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  height: 4rem;
  margin: auto;
}

ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #333;
}

.header-logo {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}

.header-logo > img {
  width: 10%;
  cursor: pointer;
}

hr {
  width: 100%;
  margin: auto;
  border: 2px solid #41adbe;
  margin-bottom: 10px;
  border-bottom: none;
}

.hamburger-menu-button {
  display: none;
  cursor: pointer;
}

.navbar-items-wrapper {
  display: flex;
}

.navbar-items-wrapper {
  display: flex;
}

.close-btn {
  display: none;
}

@media (max-width: 800px) {
  .Navbar {
    width: 100%;
    margin: auto;
  }
  nav {
    width: 100%;
    padding: 0 1rem;
  }

  .header-logo > img {
    width: 15%;
    min-width: 40px;
  }

  hr {
    width: 100%;
  }

  .hamburger-menu-button {
    display: block;
  }

  .navbar-items-wrapper {
    display: none;
  }

  .hamburger-menu-button {
    display: block;
  }

  .navbar-items-wrapper {
    display: none;
  }

  .navbar-items-wrapper.show {
    display: block;
  }

  .mobile-navbar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 110vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    z-index: 1;
    position: fixed;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    border: 3px solid #2fa3b5;
    padding: 40px;
  }

  .mobile-navbar > ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }

  .close-btn {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 2rem;
    cursor: pointer;
  }

  .close-btn:hover {
    color: #2fa3b5;
  }
}
